import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import { auth, dnsCollection } from '../firebase.config'

Vue.use(VueRouter)

const routes: RouteConfig[] = [
  {
    path: '/',
    component: () => import(/* webpackChunkName: 'Login' */ '../views/Login.vue'),
    beforeEnter (to, from, next) {
      if (auth.currentUser) {
        next(`/${auth.currentUser.uid}`)
      } else {
        next()
      }
    }
  },
  {
    path: '/404',
    component: () => import(/* webpackChunkName: '404' */ '../views/404.vue')
  },
  {
    path: '/p/:userName',
    props: true,
    component: () => import(/* webpackChunkName: 'ViewProfile' */ '../views/ViewProfile.vue'),
    async beforeEnter (to, from, next) {
      const dnsRef = dnsCollection.doc(to.params?.userName.toLowerCase())
      const profileDoc = await dnsRef.get()
      if (!profileDoc || !profileDoc.exists) {
        next('/404')
      }
      to.params.uid = profileDoc.data()?.uid
      if (!to.params.uid) {
        next('/404')
      }
      next()
    }
  },
  {
    path: '/:uid',
    props: true,
    component: () => import(/* webpackChunkName: 'ViewProfile' */ '../views/ViewProfile.vue')
  },
  {
    path: '/:uid/category/:category',
    props: true,
    component: () => import(/* webpackChunkName: 'ViewProfile' */ '../views/ViewProfile.vue')
  },
  {
    path: '/:uid/edit',
    component: () => import(/* webpackChunkName: 'EditProfile' */ '../views/EditProfile.vue'),
    meta: { requiresAuth: true, requiresOwnership: true }
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  // 404 hook
  if (to.matched.length < 1) {
    next('/404')
    return
  }

  // Auth guards hook
  const requiresAuth = to.matched.some(record => record.meta.requiresAuth)
  const isLoggedIn = auth?.currentUser

  const requiresOwnership = to.matched.some(record => record.meta.requiresOwnership)
  const isOwner = auth?.currentUser?.uid === to.params?.uid

  if (requiresAuth && !isLoggedIn) next('/')
  if (requiresOwnership && !isOwner) next('/')
  next()
})

export default router
