const socialSharePopupWidth = 600
const socialSharePopupHeight = 550

export const generateSocialShareSites = (socialShareUrl: string, socialShareText = '') => ({
  displayNames: false,
  config: [{
    facebook: {
      socialShareUrl,
      socialSharePopupWidth,
      socialSharePopupHeight
    }
  }, {
    twitter: {
      socialShareUrl,
      socialSharePopupWidth,
      socialSharePopupHeight
    }
  }, {
    reddit: {
      socialShareUrl,
      socialSharePopupWidth,
      socialSharePopupHeight
    }
  }, {
    linkedin: {
      socialShareUrl,
      socialSharePopupWidth,
      socialSharePopupHeight
    }
  }, {
    email: {
      socialShareTo: '',
      socialShareBody: socialShareUrl
    }
  }, {
    whatsapp: {
      socialShareText: `üëâ *Via https://instab.io* üëà | You've gotta üëÄ this!

${socialShareText}`,
      socialShareUrl,
      socialSharePopupWidth,
      socialSharePopupHeight
    }
  }, {
    copy: {
      socialShareUrl
    }
  }]
})
