<template>
  <div id="app">
    <router-view></router-view>
    <Footer></Footer>
    <web-social-share :show="show" @closed="show = false">
      <font-awesome-icon class="fa-2x" slot="facebook" :icon="['fab', 'facebook-f']" />
      <font-awesome-icon class="fa-2x" slot="twitter" :icon="['fab', 'twitter']" />
      <font-awesome-icon class="fa-2x" slot="reddit" :icon="['fab', 'reddit']" />
      <font-awesome-icon class="fa-2x" slot="linkedin" :icon="['fab', 'linkedin-in']" />
      <font-awesome-icon class="fa-2x" slot="email" :icon="['fa', 'envelope']" />
      <font-awesome-icon class="fa-2x" slot="whatsapp" :icon="['fab', 'whatsapp']" />
      <font-awesome-icon class="fa-2x" slot="copy" :icon="['fa', 'clipboard']" />
    </web-social-share>
  </div>
</template>

<script language="ts">
import Footer from '@/components/Footer'
import EventBus from '@/utils/EventBus'
import { generateSocialShareSites } from '@/controllers/SocialShareSites'

export default {
  metaInfo: {
    title: 'Loading...',
    titleTemplate: '%s | InstaB.io ü§©',
    meta: [
      { name: 'description', content: 'ü§© InstaB.io - A true WYSIWYG Social Media Portfolio: One link to link them all!' },
      { name: 'og:description', content: 'ü§© InstaB.io - A true WYSIWYG Social Media Portfolio: One link to link them all!' },
      { name: 'og:title', content: 'Welcome to ü§© InstaB.io ü§©' },
      { name: 'og:image', content: 'https://github.com/yeikiu/instab.io/blob/master/android-chrome-192x192.png?raw=true' },
      { name: 'keywords', content: 'social portfolio, instagram links, instagram urls, share content, post social media, wysiwyg, vuejs, github' }
    ]
  },
  components: {
    Footer
  },
  data () {
    return {
      show: false,
      shareEl: null
    }
  },
  mounted () {
    this.shareEl = document.getElementsByTagName('web-social-share')[0]
    EventBus.$on('SHOW_SOCIAL', this.showSocialShare)
  },
  methods: {
    showSocialShare ({ url, socialShareText }) {
      this.shareEl.share = generateSocialShareSites(url, socialShareText)
      this.show = true
    }
  }
}
</script>

<style lang="css">
/* Custom CSS here */

@import url(https://fonts.googleapis.com/css?family=Barlow);

html, body, #app {
  background-color: rgba(0, 0, 0, 1);
  font-family: 'Barlow', sans-serif;
}
</style>
